.wrapper {
    flex: 0 0 50px;
    background-color: #2c2c2c;
}

.normal_text {
    color: #edf3f0 !important;
}

.d_flex {
    display: flex;
}

.navbar_brand {
    flex: none;
}

.navbar_search {
    flex: auto;
    align-items: center;
    display: flex;
}

.navbar_right {
    flex: none;
    display: flex;

}

.first_name {
    color: #edf3f0 !important;
    font-size: 20px;
    vertical-align: central;
    padding: 0 7px;
}

.avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    vertical-align: middle;
}

.avatar_wrap {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.link {
    color: #e4eae7 !important;
    text-decoration: none;
    line-height: 50px;
    margin-left: 3px;
}